.login-container{
    background: url(../../imagenes/HomeLogin.png) no-repeat center center fixed;
    background-size: cover;
    width: 99.4%;
  }
  
  .login-container img{
    width: 30%;
    padding: .5rem .7rem .5rem .7rem;
    margin: .9vw 0 ;
  }
  
  .login{
    display: flex;
    align-items:center;
    justify-content: center;
    flex-direction: column;
    border: solid black 4px;
    width: 100%;
    min-height: 96vh;
  }
  .login h2{
    font-weight: 400;
    font-family: Arial, Helvetica, sans-serif;
    margin-top: 15px;
    text-align: center;
  }
  .login h3{
    font-weight: 100;
    font-family: Arial, Helvetica, sans-serif;
    margin-top: 10px;
    text-align: center;
  }
  .login form{
    background-color: rgba(255, 245,230,0.7);
    padding: 0px 30px 10px 30px ;
    text-align: center;
    font-weight: 500;
    font-family: Arial, Helvetica, sans-serif;
  }
  
  .login form button {
    display: flex;
    cursor:pointer;
    width: 50%;
    font-size: 15px;
    margin: 0 auto;
    margin-top: 3%;
    justify-content: center;
    align-items: center;
  }