body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.main-home{
    display: flex;
    border: none;
    background-color: white;
    position:static;
    align-items:center;
    justify-content: center;
    flex-direction: column;
}
.footer-style{
    margin-right: auto;
    
}

.footer-style img{
    width: 180%;
    border-radius: 20px;
}

.social-container {
    width: 99%;
    border-radius: 20px;
    align-items: center;
    text-align: center;
    margin-right: auto;
    background: #eee;
    padding: 25px 50px;
}

a.social {
    margin: 0 1rem;
    transition: -webkit-transform 250ms;
    transition: transform 250ms;
    transition: transform 250ms, -webkit-transform 250ms;
    display: inline-block;
}

a.social:hover {
    -webkit-transform: translateY(-2px);
            transform: translateY(-2px);
}
  
a.facebook {
    color: #4968ad;
}
  
a.twitter {
    color: #49a1eb;
}
  
a.instagram {
    color: black;
}

.barra-azul{
    width: 100%;
    height: 50px;
    background-color: #CBE6F1;
}
.barra-azul p{
    text-align: start;
    padding: 15px 0px 5px 165px;
}
.white-seccion{
    display: flex;
    padding: 20px 70px 30px 70px;
    justify-content: center;
    align-items: center;

}
.primer-white-seccion{
    display: flex;
    flex-direction: column;
    padding: 20px 100px 20px 100px;
}
.primer-white-seccion p{
    color: black;
    font-size: 10px;
    padding: 0;
    text-decoration: underline;
}
.primer-white-seccion h1{
    color: #3c1f1c;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 800;
}
.primer-white-seccion h4{
    color: #3c1f1c;
